<template>
  <div class="document-container upload-spec-org">
    <h6 class="document-title">
      {{ indicator.name }}
    </h6>
    <div>
      <x-form
        v-if="!achievements.length"
        :values="form"
        v-model="form"
        @submit="form => submit(indicator.id)"
        name="spec-org"
      >
        <x-input
          type="text"
          name="name"
          label="Название достижения"
          validation="required"
        >
        </x-input>
        <x-input
          type="text"
          name="full_name_head_organization"
          label="ФИО организатора"
          validation="isFullName"
          :validation-rules="{
            isFullName: ({ value }) => {
              return value.trim().split(' ').length === 3
            },
          }"
          :validation-messages="{
            isFullName: 'Введите ФИО',
          }"
        >
        </x-input>
        <x-input
          type="select"
          :options="[1, 2, 3, 4, 5, 6]"
          name="course_head_organization"
          label="Курс"
          validation="required"
        >
        </x-input>
        <x-input
          type="tel"
          name="phone_head_organization"
          label="Номер телефона"
          :validation="[['required'], ['min', '18']]"
          v-mask="`+7 (###) ###-##-##`"
        >
        </x-input>
        <x-input
          type="email"
          name="email_head_organization"
          label="E-mail"
          validation="required"
        >
        </x-input>
        <a-button
          type="info"
          block
          html-type="submit"
          class="document-container-button"
        >
          <v-icon name="plus"></v-icon>
          <span>Добавить</span>
        </a-button>
      </x-form>
      <li v-else class="achievement-list__item">
        <div class="achievement-list__item--read">
          <div class="achievement-list__item--tools">
            <a-tooltip placement="top">
              <template slot="title">
                <span>Удалить достижение</span>
              </template>
              <v-icon
                @click="deleteAchievement"
                class="remove"
                name="times"
              ></v-icon>
            </a-tooltip>
          </div>
          <div class="achievement-list__item--read--item item-name">
            <span>Название достижения</span>
            <span>{{ achievements[0].name }}</span>
          </div>
          <div class="achievement-list__item--read--item item-name">
            <span>ФИО организатора</span>
            <span>{{ achievements[0].full_name_head_organization }}</span>
          </div>
          <div class="achievement-list__item--read--item item-participation">
            <span>Курс</span>
            <span>{{ achievements[0].course_head_organization }}</span>
          </div>
          <div class="achievement-list__item--read--item item-participation">
            <span>Номер телефона</span>
            <span>{{ achievements[0].phone_head_organization }}</span>
          </div>
          <div class="achievement-list__item--read--item item-period">
            <span>E-mail</span>
            <span>{{ achievements[0].email_head_organization }}</span>
          </div>
        </div>
      </li>
    </div>
  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
import UploadSpecParticipant from './UploadSpecParticipant'
import UploadSpecDescription from './UploadSpecDescription'
import { mask } from 'vue-the-mask'

export default {
  directives: { mask },
  components: {
    FileComponent,
    UploadSpecParticipant,
    UploadSpecDescription,
  },
  props: {
    indicator: {
      type: Object,
      required: true,
    },
    saveAchievement: {
      type: Function,
      required: true,
    },
    achievements: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      form: {},
    }
  },
  methods: {
    async submit(indicators) {
      try {
        await this.saveAchievement({ ...this.form, indicators }, 'spec-org')
        this.form = {}
      } catch (e) {
        console.log(e)
      }
    },
    async deleteAchievement() {
      const { isConfirmed } = await this.$swal.fire({
        title: 'Вы точно хотите удалить достижение?',
        showCancelButton: true,
      })
      if (isConfirmed) this.$emit('remove', this.achievements[0].id)
    },
  },
}
</script>
