<template>
  <div class="document-container upload-spec-org">
    <h6 class="document-title">
      {{ indicator.name }}
    </h6>
    <div class="document-clue" v-if="indicator.hint_text">
      {{ indicator.hint_text }}
    </div>
    <div>
      <x-form
        v-if="!achievements.length"
        :values="form"
        v-model="form"
        name="spec"
        @submit="form => submit(indicator.id)"
        class="achievement-spec"
      >
        <div class="achievement-spec-block">
          <x-input
            type="text"
            name="name"
            label="Название достижения"
            validation="required"
          >
          </x-input>
        </div>
        <upload-spec-description></upload-spec-description>
        <upload-spec-participant
          :participants="participants"
          @remove-participant="removeParticipant"
          @add-participant="addParticipant"
          :participationDegree="participationDegree"
        ></upload-spec-participant>
        <div class="achievement-spec-block">
          <x-input
            type="number"
            name="number_participants"
            label="Кол-во участников"
            validation="required|number|min:0|max:50"
            min="0"
            max="200"
          >
          </x-input>
        </div>
        <div class="achievement-spec-block">
          <file-component multiple @change="file => setFiles(file)">
          </file-component>
        </div>
        <a-button
          type="info"
          block
          html-type="submit"
          class="document-container-button spec-button"
        >
          <v-icon name="plus"></v-icon>
          <span>Сохранить данные достижения</span>
        </a-button>
      </x-form>
      <li v-else class="achievement-list__item">
        <div class="achievement-list__item--read">
          <div class="achievement-list__item--tools">
            <a-tooltip placement="top">
              <template slot="title">
                <span>Удалить достижение</span>
              </template>
              <v-icon
                @click="deleteAchievement"
                class="remove"
                name="times"
              ></v-icon>
            </a-tooltip>
          </div>
          <div class="achievement-list__item--read--item item-name">
            <span>Название достижения</span>
            <span>{{ achievements[0].name }}</span>
          </div>
          <div class="achievement-list__item--read--item item-name">
            <span>Наименование ассоциации</span>
            <span>{{ achievements[0].name_association }}</span>
          </div>
          <div class="achievement-list__item--read--item item-name">
            <span>Образовательная организация</span>
            <span>{{ achievements[0].educational_organization }}</span>
          </div>
          <div class="achievement-list__item--read--item item-participation">
            <span>Год основания</span>
            <span>{{ achievements[0].year_foundation }}</span>
          </div>
          <div class="achievement-list__item--read--item item-participation">
            <span>Характеристика</span>
            <span>{{ achievements[0].brief_description_association }}</span>
          </div>

          <div class="achievement-list__item--read--item item-participation">
            <span>Участники</span>
            <ul class="participation-person-list">
              <li class="participation-person-list--item" :key="key" v-for="(participant, key) in achievements[0].full_name">
                <span>{{participant[0]}}</span>
                <span>{{participant[1]}}</span>
              </li>
            </ul>
          </div>

          <div class="achievement-list__item--read--item item-period">
            <span>Кол-во участников</span>
            <span>{{ achievements[0].number_participants }}</span>
          </div>
        </div>
        <div class="file-container" v-if="achievements[0].documents.length">
          <div class="file-container__files">
            <a
              v-for="document in achievements[0].documents"
              :key="document.id"
              :href="getDocumentLink(document.document)"
              target="_blank"
              class="file-container__files--file"
            >
              <v-icon name="file"></v-icon>
              <span>{{ document.documentName }}</span>
            </a>
          </div>
        </div>
      </li>
    </div>
  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
import UploadSpecParticipant from './UploadSpecParticipant'
import UploadSpecDescription from './UploadSpecDescription'
const { VUE_APP_API_MEDIA } = process.env

export default {
  components: {
    FileComponent,
    UploadSpecParticipant,
    UploadSpecDescription,
  },
  props: {
    participationDegree: {
      type: Array,
      required: true,
    },
    indicator: {
      type: Object,
      required: true,
    },
    saveAchievement: {
      type: Function,
      required: true,
    },
    achievements: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      media: VUE_APP_API_MEDIA,
      form: {},
      participants: [],
    }
  },
  methods: {
    getDocumentLink(path) {
      return `${this.media}/${path}`
    },
    setFiles(file) {
      this.form.documents = file
    },
    async submit(indicators) {
      try {
        
        let full_name = this.participants.map(p => [p.fullname, p.degree_participation])
        await this.saveAchievement({ ...this.form, full_name, indicators }, 'spec')
        this.form = {}
      } catch (e) {
        console.log(e)
      }
    },
    async deleteAchievement() {
      const { isConfirmed } = await this.$swal.fire({
        title: 'Вы точно хотите удалить достижение?',
        showCancelButton: true,
      })
      if (isConfirmed) this.$emit('remove', this.achievements[0].id)
    },
    addParticipant(participantForm) {
      this.participants.push(participantForm)
    },
    removeParticipant(key) {
      this.participants.splice(key, 1)
    },
  },
}
</script>

