<template>
  <ol class="achievement-spec-block spec-participant">
    <li
      class="achievement-list__item"
      v-for="(participant, key) in participants"
      :key="participant.fullname"
    >
      <div class="achievement-list__item--read">
        <div class="achievement-list__item--tools">
          <span class="count">{{ key + 1 }}</span>
          <a-tooltip placement="top">
            <template slot="title">
              <span>Удалить участника</span>
            </template>
            <v-icon
              @click="deleteParticipant(key)"
              class="remove"
              name="times"
            ></v-icon>
          </a-tooltip>
        </div>
        <div class="achievement-list__item--read--item item-name">
          <span>{{ participant.fullname }}</span>
        </div>
        <div
          class="achievement-list__item--read--item item-participation"
        >
          <span>{{ participant.degree_participation }}</span>
        </div>
      </div>
    </li>
    <li class="achievement-list__item" v-if="!loading">
      <div class="achievement-list__item--info">
        <div
          class="achievement-list__item--info--block achievement-form-multiple"
        >
          <x-form
            class="achievement-spec-block--form"
            v-model="participantForm"
            @submit="addParticipant"
            name="participantForm"
          >
          
            <div class="achievement-spec-block--form-item">
          <span class="count">{{ participants.length + 1 }}</span>
              <x-input
                name="fullname"
                type="text"
                validation="isFullName"
                label="ФИО участника"
                class="item-name"
                :validation-rules="{
                  isFullName: ({ value }) => {
                    return value.trim().split(' ').length === 3
                  },
                }"
                :validation-messages="{
                  isFullName: 'Введите ФИО',
                }"
                error-behavior="submit"
              ></x-input>
              <x-input
                name="degree_participation"
                type="select"
                validation="required"
                :options="participationDegree"
                label="Степень участия"
                class="item-participation"
                error-behavior="submit"
              >
              </x-input>
            </div>
            <a-button
              type="info"
              block
              html-type="submit"
              class="document-container-button"
            >
              <v-icon name="plus"></v-icon>
              <span>Добавить</span>
            </a-button>
          </x-form>
        </div>
      </div>
    </li>
  </ol>
</template>

<script>
export default {
  props: {
    participationDegree: {
      type: Array,
      required: true,
    },
    participants: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      participantForm: {},
    }
  },
  methods: {
    addParticipant() {
      this.loading = true
      this.$emit('add-participant', {...this.participantForm})
      setTimeout(() => {
        this.participantForm = {}
        this.loading = false
      }, 0)
    },
    deleteParticipant(key) {
      this.$emit('remove-participant', key)
    },
  },
}
</script>

